* {
  font-family: 'Catamaran', sans-serif;
  box-sizing: border-box;
}
$color-red: rgb(211, 9, 9);
$color-gray-border: rgb(235, 235, 235);
$color-gray-bg: rgb(248, 248, 248);
$color-blue-mc: #3d88fb;
body {
  background: $color-gray-bg;
}

/*--- top bar ---*/
.top-bar {
  z-index: 2000;
  position: fixed;
  left: 0;
  bottom: 0;
  right: 0;
  width: 100%;
  border-top: 1px solid rgb(235, 235, 235);
  background: white;
  .container-fluid {
    .row {
      .wrap {
        width: 100%;
        a {
          display: block;
          width: 100%;
        }
        img {
          height: 30px;
        }
      }
      .col-3.ball-icon {
        display: flex;
        align-items: center;
        justify-content: flex-start;
        padding-left: 5px;
        img {
          width: 30px;
        }
      }
      .col-6.leagues-icon {
        text-align: center;
        img {
          height: 35px;
        }
      }
      .col-3.top-bar-hot-deals {
        display: flex;
        align-items: center;
        justify-content: flex-end;
        padding-right: 5px;
        img {
          width: 100%;
        }
      }
    }
  }
}
/*--- end top bar ---*/
/*--- top nav ---*/
.top-nav {
  background: $color-red;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 100;
  a {
    color: white;
    display: flex;
    padding: 10px 5px;
    line-height: 1; /* keep all links middle vertical ^^ */
  }
  ul {
    list-style-type: none;
    margin: 0;
    padding: 0;
  }
  .icon {
    width: 18px;
    height: 18px;
  }
  .container-fluid {
    .row {
      /* col of nav including main nav and sub menu */
      .col.nav {
        .wrap {
          width: 100%;
          position: relative;
          /* main nav */
          ul.main-nav {
            display: flex;
            align-items: center;
            li {
              margin-right: 20px;
              a {
                font-weight: bold;
                span.link-text {
                  display: block;
                  margin-bottom: -3px; /* keep all links middle vertical ^^ */
                }
              }
              &:last-child {
                margin-right: 0;
              }
            }
          }
          /* open and close sub menu */
          .icon.animate__animated {
            color: white;
            display: inline-block;
            margin-right: 15px; 
            animation-duration: 1s;
          }
          /* sub menu */
          ul.sub-menu {
            animation-duration: .3s;
            z-index: 2000;
            width: 100%;
            top: 100%;
            position: absolute;
            background-image: linear-gradient(to right, rgb(126, 211, 0), white);
            li {
              a {
                color: black;
                font-weight: bold;
                font-size: 14px;
              }
            }
            .icon {
              color: black;
              position: absolute;
              top: 8px;
              right: 0;
            }
          }
          
        }
      }
      /* col for shopping cart */
      .col-auto.shopping-cart {
        position: relative;
        .wrap {
          a {
            position: relative;
            .shopping-cart-quantity {
              position: absolute;
              top: 2px;
              right: 0;
              width: 14px;
              height: 14px;
              border-radius: 50%;
              font-size: 10px;
              line-height: 14px;
              text-align: center;
              color: black;
              font-weight: bold;
              background: white;
            }
          }
        }
      }
    }
  }
}
/*--- end top nav ---*/
/*--- top slide ---*/
.top-slide {
margin-top: 40px;
  .container-fluid {
    .row {
      .col {
        padding: 0;
        .wrap {
          #top-slide{
            .carousel-inner {
              .carousel-item {
                text-align: center;
                img {
                  margin: auto;
                }
                .carousel-caption {
                  font-size: 13px;
                  position: relative;
                  left: 0;
                  right: 0;
                  bottom: 0;
                  padding: 0;
                  margin-top: 5px;
                  .link, .title {
                    background: $color-red;
                    color: white;
                    height: 20px;
                    line-height: 20px;
                    display: inline-block;
                    padding: 0 5px;
                  }
                  .link {
                    text-decoration: underline;
                    margin-left: 5px;
                  }
                }
              }
              .carousel-item.mc {
                .title {
                  background: #3d88fb;
                }
              }
              .carousel-item.tot {
                .title {
                  background: #243461;
                }
              }
            }
          }
        }
      }
    }
  }
}
/*--- end top slide ---*/
.homepage {
  margin-bottom: 10px;
}
/*--- most concerned, special promtion, top sales, champion ---*/
