  margin-bottom: 10px;
  background: $color-red;
  border-radius: 10px 10px 0 0;
  .container-fluid {
    .row {
      /* title of each section (ĐƯỢC QUAN TÂM NHIỀU NHẤT, ....) */
      .section-title {
        width: 100%;
        border-radius: 10x 10px 0 0;
        color: white;
        display: flex;
        align-items: center;
        margin-bottom: 2px;
        position: relative;
        .background-image {
          position: absolute;
          top: 0;
          left: 0;
          right: 0;
          bottom: 0;
          z-index: 1;
          border-radius: 10px 10px 0 0;
          background: {
            image: url("../src/Assets/images/title-bg/title-bg1.jpg");
            position: center;
            size: cover;
          }
        }
        .title-text {
          z-index: 2;
          padding: 0 5px;
          font-size: 15px;
          height: 30px;
          color: $color-red;
          font-weight: bold;
          display: flex;
          align-items: center;
          .icon {
            font-size: 15px;
            margin-right: 3px;
            color:  #235bf3;
            border: 1px solid #235bf3;
            padding: 1px;
            border-radius: 50%;
          }
          .football-image {
            display: inline-block;
            width: 30px;
            height: 30px;
            margin-left: 10px;
            background: {
              image: url("../src/Assets/images/title-bg/football-image1.png");
              size: cover;
              position: center;
            };
          }
        }
      }
    }
  }
}
.display-homepage.special-promotion {
  background: $color-blue-mc;
}
.display-homepage.top-sales {
  background: orange;
}
.display-homepage.champion {
  background: $color-red;
  .football-image {
    width: 20px !important;
    height: 20px !important;
    background-image: url("../src/Assets/images/title-bg/trophy.png") !important
  }
}
/* general layout of product, reuse for many component */
.col.product-col {
  flex: 50% !important;
  max-width: 50% !important;
  padding-left: 2px;
  padding-right: 1px;
  .wrap {
    background: white;
    position: relative;
    padding-top: 10px;
    margin-bottom: 3px;
    a {
      text-decoration: none;
      /* hold main image of product */
      .background-image {
        display: flex;
        img {
          margin: auto;
          width: 30vw;
          height: 30vw;
        }
      }
      /* information of product */
      .info {
        padding: 0 2px;
        font-size: 12px;
        text-align: center;
        .name {
          display: block;
          overflow: hidden;
          color: #243461;
          height: 35px;
          text-overflow: ellipsis;
          display: -webkit-box;
          -webkit-line-clamp: 2; /* show name and lines only */
          -webkit-box-orient: vertical;
        }
        .price {
          font-weight: bold;
          font-size: 13px;
          color: $color-red;
        }
      }
      /* promotion box (SALE ...%) */
      .promotion {
        position: absolute;
        top: 2px;
        left: 2px;
        background: $color-red;
        color: white;
        height: 35px;
        width: 35px;
        border-radius: 50%;
        font-size: 10px;
        text-align: center;
        display: flex;
        align-items: center;
        justify-content: center;
      }
      .free-ship {
        position: absolute;
        top: -4px;
        left: 2px;
        height: 30px;
        width: 30px;
        display: flex;
        align-items: center;
        justify-content: center;
        img {
          width: 100%;
          border: none;
        }
      }
      /* member point */
      .member-point {
        position: absolute;
        top: 2px;
        right: 2px;
        background: $color-blue-mc;
        color: white;
        height: 14px;
        width: 42px;
        font-size: 10px;
        text-align: center;
        display: flex;
        align-items: center;
        justify-content: center;
      }
    }
  }
}
/* add-to-cart-btn */
.add-to-cart-btn {
  text-align: center;
  padding-top: 0;
  span {
    font-size: 12px;
    background: $color-red;
    color: white;
    padding: 2px 3px;
  }
  span.added {
    background: greenyellow;
    border-radius: 5px;
    color: black;
    .icon {
      margin: 0 3px;
      color: rgb(93, 93, 93);
    }
  }
}
/* load more and hide */
.control-loaded-quantity {
  width: 100%;
  text-align: center;
  margin-bottom: 5px;
  .load-btn {
    border: none;
    outline: none;
    background: $color-gray-bg;
    font-size: 11px;
    border-radius: 5px;
  }
}
/* end general layout of product, reuse for many components (most concerned, special promtion, top sales, champion) */
/*--- end most concerned, special promtion, top sales, champion ---*/
