/*--- end goals-video ---*/
/*--- footer ---*/
.footer {
  padding: 10px 0 50px 0;
  background: $color-blue-mc;
  // border-radius: 10px 10px 0 0;
  .container-fluid {
    .row {
      .col {
        padding: 0 20px;
        flex: 100% !important;
        max-width: 100% !important;
        font-size: 12px;
        margin-bottom: 10px;
        line-height: 25px;
        display: flex;
        flex-direction: column;
        border-bottom: 1px solid #60a0ff;
        color: #9cc1f8;
        .title {
          font-size: 18px;
          margin: 0 0 5px -15px;
          text-transform: uppercase;
          color: #d3e4fd;
        }
        p {
          margin: 0;
        }
        .icon {
          margin-right: 5px;
        }
        a {
          color: #9cc1f8;
          text-decoration: underline;
        }
      }
      .col.social-media {
        border-bottom: none;
        padding: 10px;
        .wrap {
          border: 1px solid #60a0ff;
          position: relative;
          .title {
            margin: 0;
            background: $color-blue-mc;
            position: absolute;
            left: 30px;
            right: 30px;
            top: -10px;
            text-align: center;
          }
          .icon-link {
            padding: 20px 20px 10px 20px;
            display: flex;
            justify-content: center;
            a {
              display: inline-block;
              padding: 10px;
              border-radius: 50%;
              width: 40px;
              height: 40px;
              margin: 5px 10px;
              color: transparent;
              background: {
                image: url("../src/Assets/images/section-footer/social-media.png");
                size: 150px 150px;
                repeat: no-repeat;
              }
            }
            a.facebook {
              background-position: -12px -6px;
            }
            a.youtube {
              background-position: -100px -6px;
            }
            a.twitter {
              background-position: -12px -56px;
            }
            a.instagram {
              background-position: -100px -56px;
            }
          }
        }
      }
    }
    .copy-right {
      color: #d3e4fd;
      .col {
        border: none;
        padding: 5px;
        font-weight: bold;
      }
    }
  }
}
/*--- end footer ---*/
/*--- product detail ---*/
.product-detail {
  margin: 50px 0 10px 0;
  background: white;
  /* slide show of image at top */
  .container-fluid.slide-show {
    margin-top: 10px;
    .row {
      .col {
        padding: 0 5px;
        #ProductDetailTopSlide {
          .carousel-indicators {
            position: relative;
            align-items: center;
            margin: 0;
            padding: 0;
            li {
              width: 40px !important;
              height: 40px !important;
              margin: 5px 5px 0 5px;
              border-radius: 50%;
              border: none;
              background : {
                position: center;
                size: 80% 80%;
                repeat: no-repeat;
              }
            }
            li.active {
              border: 1px solid red;
            }
          }
          .carousel-control-prev {
            .carousel-control-prev-icon {
              background: {
                image: url("../src/Assets/images/section-product-detail/left.svg");
                position: center;
                size: cover;
                repeat: no-repeat;
              }
            }
          }
          .carousel-control-next {
            .carousel-control-next-icon {
              background: {
                image: url("../src/Assets/images/section-product-detail/right-arrow.svg");
                position: center;
                size: cover;
                repeat: no-repeat;
              }
            }
          }
        }
      }
    }
  }
  /* contain name, price, like times, view times */
  .container-fluid.product-button {
    .row {
      .col {
        padding: 0 5px;
        .wrap {
          .name {
            font-weight: bold;
          }
          .price {
            font-size: 18px;
            font-weight: bold;
            color: $color-red;
          }
          .emotions {
            display: flex;
            font-size: 11px;
            .icon {
              margin-right: 5px;
            }
            .like, .view, .share {
              background: $color-blue-mc;
              color: white;
              padding: 1px 6px;
              margin-right: 5px;
              border-radius: 5px;
            }
          }
        }
      }
    }
  }
  /* contain buttons add to cart, plus, minus, size */
  .container-fluid.product-add-to-cart {
    margin-top: 10px;
    .row {
      .col {
        padding: 0 5px;
        .wrap {
          /* display all size of product */
          .size {
            font-size: 12px;
            display: flex;
            align-items: center;
            .title {
              margin-right: 5px;
              font-weight: bold;
            }
            .size-box {
              width: 34px;
              height: 34px;
              border-radius: 50%;
              margin: 2px;
              display: flex;
              align-items: center;
              justify-content: center;
              background: $color-blue-mc;
              color: white;
            }
            .S {
              width: 30px;
              height: 30px;
            }
            .M {
              width: 31px;
              height: 31px;
            }
            .L {
              width: 32px;
              height: 32px;
            }
            .XL {
              width: 33px;
              height: 33px;
            }
          }
          /* button plus, minus, quantity */
          .buttons {
            margin: 10px 0;
            display: flex;
            align-items: center;
            .title {
              margin-right: 5px;
              font-weight: bold;
              font-size: 12px;
            }
            .minus, .quantity, .plus {
              border: none;
              outline: none;
              width: 30px;
              height: 30px;
            }
            .quantity {
              text-align: center;
              font-weight: bold;
              border-bottom: 1px solid $color-gray-border;
              border-top: 1px solid $color-gray-border;
              font-size: 12px;
            }
            /* remove default scroll of number input tag */
            input::-webkit-outer-spin-button,
            input::-webkit-inner-spin-button {
            -webkit-appearance: none;
            margin: 0;
            }
            /* Firefox */
            input[type=number] {
                -moz-appearance: textfield;
            }
            .minus, .plus {
              background: $color-red;
              color: white;
            }
          }
          /* show sub total */
          .sub-total {
            .title {
              margin-right: 5px;
              font-weight: bold;
              font-size: 12px;
            }
            .amount {
              font-size: 18px;
              font-weight: bold;
              color: $color-red;
            }
          }
          /* button add to cart and check-out */
          .btn-add-to-cart, .btn-check-out {
            button {
              height: 25px;
              border: none;
              outline: none;
              background: $color-red;
              color: white;
              font-weight: bold;
              border-radius: 15px;
              font-size: 12px;
            }
            button.check-out {
              background: $color-blue-mc;
              margin-left: 5px;
              a {
                color: white;
              }
            }
          }
        }
      }
    }
  }
  /* contain other infos of products: description, shipping, payment, refund */
  .container-fluid.product-other-info {
    margin-top: 20px;
    .row {
      .col {
        padding: 0 5px;
        .wrap {
          .info-type {
            border: 1px solid $color-gray-border;
            border-top: none;
            margin-bottom: 10px;
            .info-title {
              background: $color-gray-border;
              display: flex;
              align-items: center;
              .title {
                width: 100%;
                padding-left: 10px;
                font-size: 15px;
              }
              .accordion-btn {
                float: right;
                width: 45px;
                height: 40px;
                display: flex;
                align-items: center;
                justify-content: center;
                background: black;
                color: white;
              }
            }
            .info-detail {
              padding: 10px;
              font-size: 12px;
            }
          }
        }
      }
    }
  }
  /* also like products */
  .container-fluid.also-container {
    margin-top: 10px;
    .row {
      .col {
        padding: 0 5px;
        .wrap {
          .title {
            font-weight: bold;
          }
          .tns-outer {
            position: relative;
            .tns-controls {
              position: absolute;
              top: 35%;
              left: 0%;
              right: 0%;
              display: flex;
              justify-content: space-between;
              outline: none;
              button {
                z-index: 1;
                border: none;
                outline: none;
                color: transparent;
                background : {
                  color: transparent;
                  position: center;
                  repeat: no-repeat;
                  size: 70% 70%;
                }
              }
              button[data-controls=prev] {
                background-image: url("../src/Assets/images/section-product-detail/left.svg");
                background-position: left;
              }
              button[data-controls=next] {
                background-image: url("../src/Assets/images/section-product-detail/right-arrow.svg");
                background-position: right;
              }
            }
          }
        }
      }
    }
  }
  /* recent viewed products */
  .container-fluid.also-container.recent-viewed-container {
    .row {
      .col {
        .title {
          font-weight: bold;
        }
        .wrap.outside {
          display: flex;
          flex-wrap: wrap;
        }
      }
    }
  }
  /* general product layout used for also like and recent viewed*/
  .also-product {
    padding-left: 2px;
    padding-right: 2px;
