          }
        }
        /* information of product */
        .info {
          padding: 0 2px;
          font-size: 12px;
          text-align: center;
          .name {
            display: block;
            overflow: hidden;
            color: #243461;
            height: 35px;
            text-overflow: ellipsis;
            display: -webkit-box;
            -webkit-line-clamp: 2; /* show name and 2 lines only */
            -webkit-box-orient: vertical;
          }
          .price {
            font-weight: bold;
            font-size: 13px;
            color: $color-red;
          }
        }
        /* promotion box (SALE ...%) */
        .promotion {
          position: absolute;
          top: 2px;
          left: 2px;
          background: $color-red;
          color: white;
          height: 35px;
          width: 35px;
          border-radius: 50%;
          font-size: 10px;
          text-align: center;
          display: flex;
          align-items: center;
          justify-content: center;
        }
        /* free ship car icon */
        .free-ship {
          position: absolute;
          top: -4px;
          left: 2px;
          height: 30px;
          width: 30px;
          display: flex;
          align-items: center;
          justify-content: center;
          img {
            width: 100%;
            border: none;
          }
        }
        /* member point */
        .member-point {
          position: absolute;
          top: 2px;
