          text-align: center;
          display: flex;
          align-items: center;
          justify-content: center;
        }
      }
    }
  }
  .also-product.recent-viewed {
    width: 50%;
  }
  /* end general product layout used for also like and recent viewed*/
}
/*--- end product detail ---*/
/*--- leagues ---*/
.leagues {
  margin-top: 38px;
  /* hold main menu on the left side */
  .leagues-nav {
    background: rgb(126, 211, 0);
    border-right: 1px solid $color-gray-border;
    position: fixed;
    top: 38px;
    left: 0;
    bottom: 0;
    z-index: 1;
    padding-bottom: 35px;
    .wrap {
      height: 100%;
      display: flex;
      flex-direction: column;
      justify-content: space-evenly;
      padding: 0 2px;
      .links {
        padding-bottom: 5px;
        img {
          width: 11vw;
        }
      }
    }
  }
  /* show product of each league */
  .leagues-products {
    margin-left: 13vw;
    .leagues-products-wrap {
      .each-league-title {
        font-weight: bold;
        color: white;
        font-size: 14px;
        padding: 10px 0;
        margin: 4px;
        background: {
          size: auto 60%;
          position: right;
          repeat: no-repeat;
        }
      }
      .epl {
